







































import { Component, Prop, Watch, Emit, Vue } from 'vue-property-decorator';
import CFormula from '@/components/links/CFormula.vue';
import MathUtil from '@/typescript/MathUtil.ts';

@Component({
  components: {
    CFormula,
  },
  head: {
    title: {
      inner: '2点間の距離',
    },
  },
})
export default class Distance extends Vue {
  // Data
  private dataLinkTitle: string = '関連リンク一覧';
  private dataX1: number = 0;
  private dataY1: number = 0;
  private dataZ1: number = 0;
  private dataX2: number = 0;
  private dataY2: number = 0;
  private dataZ2: number = 0;
  private dataDistance: number = 0;

  // Methods
  private listenInputNumber(): void {
    let distance: number = Math.sqrt(
      Math.pow(Number(this.dataX2) - Number(this.dataX1), 2) +
      Math.pow(Number(this.dataY2) - Number(this.dataY1), 2) +
      Math.pow(Number(this.dataZ2) - Number(this.dataZ1), 2)
    );
    this.dataDistance = MathUtil.round(distance, this.countMaxDecimlPart() + 1);
  }
  private countMaxDecimlPart(): number {
    let coordinates: number[] = [];
    coordinates.push(this.dataX1);
    coordinates.push(this.dataY1);
    coordinates.push(this.dataZ1);
    coordinates.push(this.dataX2);
    coordinates.push(this.dataY2);
    coordinates.push(this.dataZ2);
    let maxDecimalPart: number = 0;
    coordinates.forEach((coordinate: number): void => {
      let decimalPart: number = MathUtil.countDecimlPart(coordinate);
      if (maxDecimalPart < decimalPart) {
        maxDecimalPart = decimalPart;
      }
    });
    return maxDecimalPart;
  }
}
